.cta {
  background-color: $lightBlue;
  border-radius: 15px;
  padding: 60px 110px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  @include media-query('tablet') {
    justify-content: center;
  }
  @include media-query('mobile') {
    padding: 40px 30px;
  }
  h3 {
    margin-bottom: 15px;
  }
  h2 {
    margin: 0;
    @include media-query('medium') {
      text-align: center;
      margin-bottom: 12px;
    }
    span {
    @include media-query('tablet') {
      display: block;
    }
    }
    img { 
      margin: -24px 20px 0 0px;
      position: relative;
      top: 0;
      max-width: 60px;
      float: left;
      @include media-query('tablet') {
        float: none;
        display: inline-block;
        margin: 0 0 15px 0;
      }
    }
  }
  .cta-left{
    @include media-query('tablet') {
      width: 100%;
    }
  }
  .cta-right{
    @include media-query('tablet') {
      width: 100%;
      text-align: center;
    }
  }
}
.cta-right{ 
  a{
    &:hover{
      border-color: #011f5b;
    }
  }
}
.space {
	&.cta-boxes {
		&.layout {
			padding-bottom: 0;
		}
	}
}

.mac-os{
  .cta-right{ 
    .btn-secondary {
      padding: 15px 20px 18px 20px;
    }
  }
}

.col-out-block {
  padding: 40px;
  border-radius: 20px;
  margin-bottom: 60px;
  @include media-query('small') {
    padding: 25px;
    margin-bottom: 40px;
  }
  h2 {
    margin-bottom: 10px;
  }
  &.blue {
    background-color: $lightBlue;
  }
  &.yellow {
    background-color: $yellowLightColor;
  }
}