.user-logged-in {
  .views-element-container {
    max-width: 1300px;
    margin-left: auto;
    margin-right: auto;
  }

  nav.tabs:has(.nav.primary.nav-tabs) {
    background: #e9ecef;
  }
  nav.tabs .nav.primary.nav-tabs {
    max-width: 1300px;
    margin: 0 auto;
    border: 0;
  }

  .form-type-duration .duration-inner-wrapper .form-type--number, 
  .form-type-duration .duration-inner-wrapper .form-type-number, 
  .form-type--duration .duration-inner-wrapper .form-type--number, 
  .form-type--duration .duration-inner-wrapper .form-type-number {
    width: auto;
    margin-bottom: 0 !important;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
    input {
      padding: 10px 15px;
      height: 50px;
      min-height: 50px;
      min-width: 125px;
      &::placeholder {
        color: $primaryColor;
        opacity: 1;
      }
    }
  }
  .js-form-type-duration {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column-reverse;
 
    label {
      width: 100%;
      position: static;
      height: auto;
      padding: 0;
      margin: 0;
      transform: none !important;
      opacity: 1;
      color: $primaryColor;
    }
  }
 
  .input-group-prepend {
    span {
      height: 51px;
      position: absolute;
      left: -8px;
      top: 1px;
      z-index: 1;
      border: 0;
      background: none;
    }
  }
  .input-group > :not(:first-child):not(.dropdown-menu):not(.valid-tooltip):not(.valid-feedback):not(.invalid-tooltip):not(.invalid-feedback) {
    margin-left: calc(var(--bs-border-width) * -1);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
  }

