.ck.ck-editor__main>.ck-editor__editable {
  background: unset;
}

.bs-bg-dark {
  background: black;
}

.bs-site-black {
  color: #181716;
}

#layout-builder-modal summary,
#drupal-off-canvas summary[aria-expanded='true'],
#drupal-off-canvas summary:hover,
#drupal-off-canvas summary:focus {
  color: #ffffff;
  background-color: rgba(38, 165, 255, 0.14) !important;
}


#layout-builder-modal summary,
#drupal-off-canvas summary,
#drupal-off-canvas summary:hover,
#drupal-off-canvas summary:focus {
  background-color: unset;
}

#layout-builder-modal .insurance-library-image {
  position: relative;
  margin: 0 auto;
  text-align: center;
}

