.event-modal {
  .modal-dialog {
    max-width: 400px;
  }
  .modal-header {
    border: 0;
    padding: 0;
    margin-bottom: 30px;
    display: flex;
    justify-content: space-between;
    .btn-close {
      background: none;
    }
    a {
      font-size: 12px;
      text-transform: uppercase;
      font-weight: 600;      
      text-decoration: none;
      display: inline-flex;
      align-items: center;
      gap: 4px;
      font-family: $teritoryFont;
      img {
        position: relative;
        top: -2px;
      }
    }

    .modal-header-left  {
      display: inline-flex;
      gap: 15px;
    }

    .modal-header-right  {
      button {
        padding: 0;
        opacity: 1;
      }
    }
  }
  .modal-content {
    border-radius: 10px;
    max-height: 690px;
    overflow: scroll;
  }
  .event-detail {
    margin-bottom: 15px;
  }
  .event-details {
    padding: 0;
  }
  .col-lg-8,
  .col-lg-4 {
    width: 100%;
  }
  h1 {
    font-size: 36px;
    line-height: 46px;
    font-weight: 700;
  }
  .event-detail-left {
    ul {
      li {
        margin-bottom: 0;
      }
    }
    
    p {
      font-size: 20px;
      line-height: 24px;
    }
    
    .btns-wrapper {
      flex-wrap: wrap;
      margin-top: 20px;
      gap: 15px;
      .blue-tpt,
      .blue-filled {
        font-size: 16px;
        padding: 14px 20px 11px 20px;
      }
    }
    
  }
}

.calendar-modal {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  .modal-content {
    width: 300px;
    height: 300px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    padding: 15px 10px;
    border: 0;
  }
  .close-btn {
    background: url('../../assets/images/close-icon.svg')no-repeat;
    height: 21px;
    width: 18px;
    padding: 0;
    text-indent: -9999px;
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
  }
  h2 {
    text-align: center;
    font-size: 24px;
    line-height: 34px;
    font-family: $teritoryFont;
  }
  p {
    font-family: $teritoryFont;
    font-size: 16px;
    line-height: 24px;
    margin: 0 0 8px 0;
  }
}


.ui-dialog:has(.node--type-chas-event) {
  padding: 0;
  border: 0;
  border-radius: 8px;
  max-width: 400px;
  .ui-dialog-titlebar {
    padding: 0;
    background: none;
    border: 0;
    
    .ui-dialog-title {
      display: none;
    }
  }

  .ui-dialog-titlebar-close {
    background-color: transparent;
    border: 0;
    background-image: url(../../images/close.svg);
    background-repeat: no-repeat;
    position: absolute;
    right: 10px;
    top: 20px;
    opacity: 1;
    z-index: 111;
    cursor: pointer;
  }

  .event-details-breadcrumbs  {
    margin-top: 0;
  }

  
  .ui-dialog-content {
    padding: 0;
    article,
    .node--unpublished {
      padding: 18px 30px 30px 30px;
    }

    .event-details-wrap {
      padding-top: 15px !important;
    }

    .block-field-blocknodechas-eventtitle {
      margin-bottom: 10px;
    }

    .event-breadcrumbs {
      li:first-child {
        display: none;
      }
      ul {
        
        a {
          color: $primaryColor; 
          display: inline-flex;
          align-items: center;
          gap: 3px;
          padding-left: 0;
          img {
            position: relative;
            top: -2px;
          }
        }
      }
    }
    h1 {
      font-size: 48px;
      line-height: 48px;
      color : $primaryColor;
    }
    .container {
      padding: 0;
    }
    .field--name-field-event-schedule {
      img {
        margin-right: 15px;
      }
      time {
        color: $primaryColor;
        font-size: 12px;
        font-weight: 600;
        text-decoration: underline;
       text-decoration-color: $lightBlue;
       font-family: $teritoryFont;
      }
    }
    .field--name-field-text-box-2 {
      img {
        margin-right: 15px;
      }
      .event-detail-location {
        color: $primaryColor;
        font-size: 12px;
        font-weight: 600;
        text-decoration: underline;
        text-decoration-color: $lightBlue;
        font-family: $teritoryFont;
      }
    }
    .event-details-wrap .block-field-blocknodechas-eventfield-text-box-2 {
      margin-bottom: 15px;
    }

    .content {
      color: $primaryColor;
      font-family: $primaryFont;
      a {
        color: #fff;
        &:hover {
          color: $primaryColor;
        }
      }
    }

   

  }
}

 